@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500&family=Roboto+Mono:wght@300;400&family=Roboto:wght@700&display=swap);
:root {
  /* --primary-background-color: #0f0f0f; */
  /* --primary-background-color: #141414; */
  --primary-background-color: #000;
  --primary-font-color: #fff;
  --secondary-font-color: #bbb;
}

*,
html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  cursor: none;
}

body {
  font-family: "Inter", sans-serif;
  background: #000;
  background: var(--primary-background-color);
  color: #fff;
  color: var(--primary-font-color);
  position: relative;
  line-height: 1.6;
  height: 100%;
  min-height: 100vh;
  width: 100%;
  /* font-family: 'Roboto', sans-serif;
font-family: 'Roboto Mono', monospace; */
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: #000;
}

::-webkit-scrollbar-thumb {
  background: #555;
}

.intro__page {
  position: relative;
  width: 100%;
  min-height: 100vh;
  /* border: 1px solid #fff; */
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.signature__background {
  position: absolute;
  top: 0;
  width: 20%;
  height: 100vh;
  background: #111111;
  z-index: -1;
  right: 0;
}

.scroll__down {
  position: absolute;
  top: 97%;
  left: 50%;
  transform: translate(-50%, -90%);
}

.scroll__down .indicator {
  position: relative;
  width: 20px;
  height: 20px;
  transform: rotate(45deg);
}

.indicator span {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: none;
  border-right: 2px solid #fff;
  border-bottom: 2px solid #fff;
  animation: scroll 1.4s ease-in infinite;
}

.indicator span:nth-child(1) {
  top: -20px;
  left: -20px;
  animation-delay: 0s;
}

.indicator span:nth-child(2) {
  top: -10px;
  left: -10px;
  animation-delay: 0.2s;
}

.indicator span:nth-child(3) {
  top: 0px;
  left: 0px;
  animation-delay: 0.4s;
}

@keyframes scroll {
  0% {
    border-color: #fff;
  }
  20% {
    border-color: #fff;
  }
  20.1%,
  100% {
    border-color: #686868;
  }
}

.intro__tagline {
  padding: 10px;
  padding-left: 1rem;
}

.hello {
  width: 45%;
}

.quote {
  font-family: "Roboto", sans-serif;
  font-size: 4rem;
}

.quote span {
  background: linear-gradient(to right, #eb04ff, #ff4f00);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.signature {
  /* border: 1px solid #fff; */
  z-index: 2;
  width: 75%;
  height: 35vh;
  display: flex;
  justify-content: center;
  transition: all 1s ease;
  transition-delay: 1s;
}

.signature svg {
  /* border: 1px solid #fff; */
  width: 100%;
  height: 100%;
}

.signature svg .text {
  stroke-dasharray: 800;
  stroke-dashoffset: 800;
  animation: animate 6s ease 1 forwards;
  animation-delay: 1s;
}

@keyframes animate {
  to {
    stroke-dashoffset: 0;
  }
}

.shadow {
  -webkit-filter: drop-shadow(3px 3px 2px rgba(236, 26, 243, 0.7));
  filter: drop-shadow(3px 3px 2px rgba(70, 69, 70, 0.9));
}

@media only screen and (max-width: 768px) {
  .intro__page {
    flex-direction: column-reverse;
    justify-content: space-around;
  }
  .signature {
    width: 60%;
    height: 60%;
    margin-top: 4rem;
  }

  .intro__tagline {
    padding: 0 2rem;
  }
  .quote {
    font-size: 3.7rem;
  }

  .scroll__down .indicator {
    width: 15px;
    height: 15px;
  }
}

@media only screen and (max-width: 500px) {
  .intro__tagline {
    text-align: center;
    padding: 0 1rem;
  }
  .quote {
    font-size: 2.5rem;
  }
}

.navbar {
  width: 100%;
  min-height: 8vh;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.navbar::after {
  content: "";
  position: absolute;
  top: 0;
  left: 100%;
  content: "";
  width: 1px;
  height: 100vh;
  background: rgba(170, 170, 170, 0.2);
}

.navbar__logo,
.scroll__to__top {
  flex: 0.15 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar__logo .logo {
  height: 50%;
  width: 50%;
}
.navbar__links {
  width: 100%;
  flex: 0.7 1;
}

.navbar__links ul {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.link > a {
  color: var(--secondary-font-color);
  letter-spacing: 0.05rem;
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: 0.9rem;
}

.link.active > a {
  color: var(--primary-font-color);
}

.link {
  width: 100%;
  position: relative;
}

.link::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 3px;
  top: 0;
  left: 99%;
  background: #fff;
  opacity: 0;
  transition: all 0.4s ease-in;
}
.link.active::after {
  opacity: 1;
}

.link .link__number {
  font-family: "Roboto Mono", monospace;
}

.link .link__name {
  transform: translateY(20px);
  transition: all 0.4s ease-in;
  opacity: 0;
}

.link.active .link__name {
  transform: translateX(0px);
  opacity: 1;
}

.scroll__to__top svg {
  color: var(--secondary-font-color);
  font-size: 2rem;
  cursor: pointer;
  transition: all 0.3s ease-in;
}

.scroll__to__top.active svg {
  transform: rotate(180deg);
}
.burger {
  display: none;
  cursor: pointer;
}
.burger div {
  border-radius: 10px;
  height: 2px;
  background: #fff;
  transition: all 0.3s ease-in;
}

.burger .line1 {
  margin-bottom: 8px;
  width: 10px;
}
.burger .line2 {
  margin-bottom: 8px;
  width: 20px;
}
.burger .line3 {
  width: 30px;
}

.toggle .line1 {
  margin-bottom: 0;
  opacity: 0;
}

.toggle .line2 {
  margin-bottom: 0;
  width: 30px;
  transform: rotate(-45deg) translate(4px, 2px);
}

.toggle .line3 {
  width: 30px;
  transform: rotate(45deg) translate(1px, -5px);
}

@media only screen and (max-width: 768px) {
  .navbar {
    z-index: 3;
    width: 100%;
    height: 10vh;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    background: var(--primary-background-color);
  }

  .navbar::after {
    width: 0px;
    height: 0vh;
  }

  .navbar__links {
    background: var(--primary-background-color);
    position: absolute;
    top: 100vh;
    height: 100vh;
    left: 0;
    transition: all 0.4s ease;
  }

  .navbar__links.active {
    top: 0vh;
  }

  .burger {
    display: block;
    z-index: 5;
    padding-right: 10px;
  }
  .scroll__to__top {
    display: none;
  }

  .navbar__logo .logo {
    height: 60%;
    width: 60%;
  }
}

@media only screen and (max-width: 500px) {
  .navbar__logo .logo {
    height: 80%;
    width: 80%;
  }
}

.about__page {
  position: relative;
  margin-top: 25vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  /* border: 1px solid #fff; */
}

.profile {
  position: absolute;
  top: 0;
  right: 0;
  height: 100vh;
}

.heading {
  position: relative;
  margin: 0;
  padding: 0;
  font-size: 3rem;
  letter-spacing: 0.1rem;
  margin: 1rem 0 1rem 2rem;
  display: inline-block;
  transition: all 0.4s ease-in;
  transform-origin: left;
  transform: scaleX(0);
}

.heading::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, #eb04ff, #ff4f00);
  transform-origin: right;
  transition: all 0.4s ease-in;
}

.heading[data-scroll="in"] {
  transform: scaleX(1);
}

.heading[data-scroll="in"]:before {
  transform: scaleX(0);
  transition-delay: 0.5s;
}

.about__body {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.about__body .content {
  width: 50%;
}

.content .type {
  background: linear-gradient(to right, #eb04ff, #ff4f00);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 500;
}

.content > p {
  font-size: 1.2rem;
  color: var(--secondary-font-color);
  letter-spacing: 0.01rem;
  margin-left: 2rem;
}

.content .resume {
  color: var(--primary-font-color);
  margin-top: 2rem;
  font-size: 1.1rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  letter-spacing: 1px;
  border: 1px solid #fff;
  padding: 10px 20px;
}

.resume svg {
  margin-left: 0.5rem;
  transition: all 0.3s ease-in-out;
}

.resume:hover > svg {
  transform: rotate(90deg);
}

@media only screen and (max-width: 768px) {
  .about__page {
    margin-top: 15vh;
    padding: 0;
    width: 100%;
  }
  .heading {
    margin-left: 1rem;
    font-size: 2rem;
  }

  .about__body .content {
    width: 70%;
  }

  .content > p {
    font-size: 1rem;
    margin-left: 1rem;
  }

  .content .resume {
    display: inline-flex;
    font-size: 1rem;
    padding: 8px 18px;
  }
}

.skills__page {
