.card {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
  margin-bottom: 10rem;
}

.card.even {
  flex-direction: row-reverse;
}

.card__left .card__container {
  position: relative;
  min-width: 380px;
  min-height: 280px;
  transition: all 0.2s ease-in;
  overflow: hidden;
  margin: 0 20px;
  cursor: pointer;
}

.card__container .image__box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.image__box img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  object-fit: cover;
}

.card__circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  height: 200px;
  width: 200px;
  border-radius: 100%;
  background: #fff;
  transition: 0.3s;
}

.card__container:hover {
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  box-shadow: 0 10px 18px rgba(0, 0, 0, 0.3);
  transition: all 0.2s ease-out;
}

.card__container:hover .card__circle {
  transform: translate(-50%, -50%) scale(1);
}

.text__wrapper {
  position: absolute;
  top: 50%;
  left: -20%;
  letter-spacing: 1px;
  transform: translate(0, -50%);
  font-size: 1.5rem;
  font-weight: 500;
  text-transform: uppercase;
  white-space: nowrap;
  transition: 0.1s ease-out;
  animation: float-left 20s linear infinite;
  animation-delay: 0.3s;
  z-index: 2;
  padding: 20px 0;
  mix-blend-mode: difference;
  opacity: 0;
}

@keyframes float-left {
  0% {
    left: -20%;
  }
  50% {
    left: -100%;
  }
  100% {
    left: -20%;
  }
}

.card__container:hover .text__wrapper {
  opacity: 1;
}

.card__right {
  width: 50%;
  padding: 20px;
}

.card__right > span {
  font-size: 1.1rem;
  margin-bottom: 1rem;
  color: var(--secondary-font-color);
}

.card__right h2 {
  font-size: 1.7rem;
  letter-spacing: 1px;
}

.card__right p {
  font-size: 1.1rem;
  color: var(--secondary-font-color);
}

.code__links {
  font-size: 1.8rem;
  margin-top: 1.5rem;
  width: 15%;
  display: flex;
  justify-content: space-between;
}

.code__links a {
  transition: all 0.3s ease-in;
}

.code__links a:hover {
  color: #fff;
}

.react-icons {
  color: #bbb;
}

@media only screen and (max-width: 768px) {
  .card {
    flex-direction: column;
  }
  .card.even {
    flex-direction: column;
  }
  .card__left {
    margin: 20px 0;
  }
  .card__container {
    margin: 0 !important;
  }
  .card__right {
    width: 90%;
    padding: 10px;
    text-align: justify;
  }

  .code__links {
    width: 23%;
  }
}

